    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* border: 1px solid purple; */
}

:root {
    --button-text-color: rgb(0, 0, 0);
    --text-color: #ffffff;
    --background-color: #141414;
    --primary-color: #b83d14;
    --secondary-color: #0e0e0e;
    --accent-color: #ca4316;
    --priority-high-color: #ff0000;
    --priority-mid-color: #ffd900;
    --priority-low-color: #77ff00;
    font-family: "Inter";
    font-size: large;
    font-weight: 600;
}

button {
    color: var(--text-color);
    font-family: "Inter";
    font-size: 13px;
    letter-spacing: 2px;
    padding: 10px;
    margin-left: 5px;
    margin-right: 5px;
    border: 3px solid var(--accent-color);
    border-radius: 34px;
    background: var(--background-color);
    justify-content: center;
    align-items: center;
}

button:hover {
    cursor: pointer;
    background: var(--accent-color);
    color: var(--button-text-color);
}

#content {
    color: var(--text-color);
    background-color: var(--background-color);
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr 5fr;
    grid-template-rows: 1fr 14fr;
}

#createItemContainer,
#createFolderContainer,
#areYouSureContainer {
    color: var(--text-color);
    background-color: var(--background-color);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#createItemContainer::before,
#createFolderContainer::before,
#areYouSureContainer::before {
    content: "";
    position: absolute;
    top: -20px;
    right: -20px;
    bottom: -20px;
    left: -20px;
    z-index: -1;
    background-color: var(--background-color);
    border: 3px solid var(--accent-color);
    border-radius: 8px;
}

/* todo stuff */
#todoElements {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(10, 1fr);
    grid-row: 2;
    grid-column: 2 / span 2;
    background-color: var(--secondary-color);
    gap: 2px;
    overflow: scroll;
    padding: 10px;
}

.todoElement {
    display: grid;
    grid-template-columns: 8fr 2fr 1fr;
    justify-content: right;
    align-items: center;
    margin: 10px;
    border-left: 3px solid var(--accent-color);
    border-radius: 10px;
    background-color: var(--background-color);
    flex-grow: 0;
    gap: 10px;
}

.todoElementTitle {
    font-size: large;
    font-weight: 900;
    margin: 10px;
    margin-left: 20px;
}

/* folders */
#folders {
    display: flex;
    flex-direction: column;
    grid-row: 2;
    grid-column: 1;
    padding-top: 20px;
    gap: 5px;
    overflow: scroll;
}

.folderContainers {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid transparent;
    min-height: 40px;
}

.folderContainers:hover {
    background-color: var(--secondary-color);
    border: 1px solid var(--accent-color);
}

.folderContainers.selected {
    border: 1px solid var(--accent-color);
}

.folderTitles {
    margin-left: 10px;
}

.removeFolderButton {
    margin-right: 10px;
}

#createFolderTextInput,
#titleInput,
#dueDateInput {
    margin: 10px;
    padding: 10px;
    border-radius: 8px;
    background-color: var(--background-color);
    color: var(--text-color);
    border: 2px solid var(--accent-color);
    font-size: large;
    outline: none;
}

#areYouSureButtonContainer {
    display: flex;
    margin: 10px;
    justify-content: center;
    align-items: center;
}

.radioContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 5px;
}

.radioContainer > input[type="radio"] {
    margin-left: 10px;
}

.radioContainer > label {
    justify-content: center;
    align-items: center;
}

.buttonContainer {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* new items and folders button */
#openNewItemDialogueButton,
#newFolderDialogueButton {
    font-size: larger;
    margin: 10px;
}

#topBarContainer {
    grid-column: 1 / span 3;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    border-bottom: 1px solid var(--accent-color);
}

.logoText {
    grid-column: 1;
    display: flex;
    align-items: center;
    padding-left: 20px;
    font-size: 50px;
    letter-spacing: 5px;
}

#newFolderDialogueButton {
    grid-column: 4;
}

.priorityColorClassHigh {
    border-left: 5px solid var(--priority-high-color);
}

.priorityColorClassMid {
    border-left: 5px solid var(--priority-mid-color);
}

.priorityColorClassLow {
    border-left: 5px solid var(--priority-low-color);
}
